<template>
    <div class="BookImmediately">
        <main>
            <div class="title">
                <h3></h3>
                <p>
                    <span>:0915</span>
                    <span>0916</span>1</p>
            </div>
            <div class="weui-cells weui-cells_form mb20">
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        <InputNumber :num="num" :minnum="minnum" :maxnum="maxnum" :changefn="changefn"></InputNumber>
                    </div>
                </div>
                <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="Choicetimefn">
                    <div class="weui-cell__hd">
                        
                    </div>
                    <div class="weui-cell__bd">
                        <p v-if="!Choicetime"></p>
                        {{Choicetime}}
                    </div>
                </div>
            </div>
            <div class="weui-cells weui-cells_form mb20">
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="text" placeholder="">
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="number" placeholder="">
                    </div>
                </div>

                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="number" placeholder="">
                    </div>
                </div>
            </div>
            <div class="weui-cells weui-cells_form">
                <div class="weui-cell weui-cell_select weui-cell_select-after">
                    <div class="weui-cell__hd">
                        <label for="" class="weui-label">
                            <div class="Prompt red">55.00</div>
                        </label>
                    </div>
                    <div class="weui-cell__bd">
                    </div>
                </div>
                <div class="Coupons">
                    <div class="Coupon" v-for="(item,index) in Coupon" :class="{ active: isActive==index }" @click="switchfn(index)">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-icon-test"></use>
                        </svg>
                        <span>{{item.text}}</span>
                    </div>
                </div>
                <div class="weui-cell weui-cell_switch">
                    <div class="weui-cell__bd">
                        <div class="Prompt red">20002.00</div>
                    </div>
                    <div class="weui-cell__ft">
                        <label for="switchCP" class="weui-switch-cp">
                            <input id="switchCP" class="weui-switch-cp__input" type="checkbox" disabled>
                            <div class="weui-switch-cp__box"></div>
                        </label>
                    </div>
                </div>
                <div class="weui-cell weui-cell_switch">
                    <div class="weui-cell__bd">
                        <div class="Prompt"></div>
                    </div>
                    <div class="weui-cell__ft">
                        <label for="switchCP1" class="weui-switch-cp">
                            <input id="switchCP1" v-model="abc" class="weui-switch-cp__input" type="checkbox">
                            <div class="weui-switch-cp__box"></div>
                        </label>
                    </div>
                </div>
                <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="abcd">
                    <div class="weui-cell__hd">
                        
                    </div>
                    <div class="weui-cell__bd">
                        <p v-if="!PaymentMethod"></p>
                        {{PaymentMethod}}
                    </div>
                </div>
            </div>
            <div class="Settlement">
                <div class="weui-cells">
                    <div class="weui-cell">
                        <div class="weui-cell__bd">
                            <p></p>
                        </div>
                        <div class="weui-cell__ft">1,338.00</div>
                    </div>
                </div>
                <div class="weui-cells">
                    <div class="weui-cell">
                        <div class="weui-cell__bd">
                            <p></p>
                        </div>
                        <div class="weui-cell__ft">1,338.00</div>
                    </div>
                </div>
                <div class="weui-cells">
                    <div class="weui-cell">
                        <div class="weui-cell__bd">
                            <p></p>
                        </div>
                        <div class="weui-cell__ft">1,338.00</div>
                    </div>
                </div>

                <div class="weui-cells">
                    <div class="weui-cell">
                        <div class="weui-cell__bd">
                            <p></p>
                        </div>
                        <div class="weui-cell__ft">1,338.00</div>
                    </div>
                </div>
                <div class="weui-cells">
                    <div class="weui-cell">
                        <div class="weui-cell__bd">
                            <p></p>
                        </div>
                        <div class="weui-cell__ft">1,338.00</div>
                    </div>
                </div>
            </div>
        </main>
        <section class="placeOrder">
            <router-link tag="a" to="/payment" class="weui-btn weui-btn_primary fr">
                
            </router-link>
            <div class="placeOrder-text">
                <span class="red">1,631.00</span>
            </div>
        </section>
    </div>
</template>

<script type='text/ecmascript-6'>
import InputNumber from "@/components/InputNumber"
import weui from "@/assets/js/weui.min";
export default {
    data() {
        return {
            value: '',
            abc: true,
            num: 3,
            minnum: 0,
            maxnum: 10,
            Coupon: [{ text: '10', id: "1" }, { text: '50', id: "2" }],
            isActive: 1,
            PaymentMethod: '',
            Choicetime: ''
        }
    },
    methods: {
        changefn(val) {
            console.log(val)
        },
        switchfn(index) {
            this.isActive = index
        },
        abcd() {
            var _this = this;
            weui.picker([{
                label: '',
                value: 0
            }, {
                label: '',
                value: 1
            }, {
                label: '',
                value: 2
            }], {
                    onChange: function(result) {
                        // console.log(result, '1111');
                    },
                    onConfirm: function(result) {
                        _this.PaymentMethod = result[0].label
                    }
                });
        },
        Choicetimefn() {
            var _this = this;
            weui.picker([{
                label: '19:00',
                value: 0
            }, {
                label: '19:30',
                value: 1
            }, {
                label: '19:30',
                value: 2
            }], {
                    onChange: function(result) {
                        // console.log(result, '1111');
                    },
                    onConfirm: function(result) {
                        _this.Choicetime = result[0].label
                    }
                });
        }
    },
    components: {
        InputNumber
    }
}
</script>

<style scoped lang='less' rel='stylesheet/less'>
.BookImmediately {
    display: flex;
    flex-flow: column;
    min-height: 100vh;
}

main {
    flex: 1;
    background: #f4f4f4;
}
</style>



